import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import webdesign from '../views/webdesign.vue'
import webhosting from '../views/webhosting.vue'
import logodesign from '../views/logodesign.vue'
import wordpress from '../views/wordpress.vue'
import webredesign from '../views/webredesign.vue'
import websitemaintainance from '../views/websitemaintainance.vue'
import photography from '../views/photography.vue'
import seo from '../views/seo'
import websitemonetization from '../views/websitemonetization.vue'
import templates from '../views/templates'
import packages from '../views/packages/packages.vue'
import business from '../views/packages/business.vue'
import entertainment from '../views/packages/entertainment.vue'
import institutions from '../views/packages/institutions.vue'
import personal from '../views/packages/personal.vue'
import singlePackage from '../views/packages/singlePackage.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/about',
    name: 'about',
    component: About
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
   // component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/templates',
    name: 'templates',
    component: templates
  },
  {
    path: '/webdesign',
    name: 'webdesign',
    component: webdesign,
  },
  {
    path: '/webdesign/packages',
    name: 'packages',
    component: packages,
    children: [
      {
        path: '/webdesign/packages/business',
        name: 'business',
        component: business,
      },
      {
        path: '/webdesign/packages/entertainment',
        name: 'entertainment',
        component: entertainment,
      },
      {
        path: '/webdesign/packages/institutions',
        name: 'institutions',
        component: institutions,
      },
      {
        path: '/webdesign/packages/personal',
        name: 'personal',
        component: personal,
      },
    ]
  },

  {
    path: '/webhosting',
    name: 'webhosting',
    component: webhosting,
  },
  {
    path: '/logodesign',
    name: 'logodesign',
    component: logodesign
  },
  {
    path: '/wordpress',
    name: 'wordpress',
    component: wordpress
  },
  {
    path: '/webredesign',
    name: 'webredesign',
    component: webredesign
  },
  {
    path: '/websitemaintainance',
    name: 'websitemaintainance',
    component: websitemaintainance
  },
  {
    path: '/photography',
    name: 'photography',
    component: photography
  },
  {
    path: '/seo',
    name: 'seo',
    component: seo
  },
  {
    path: '/websitemonetization',
    name: 'websitemonetization',
    component: websitemonetization
  },
  {
    path: '/singlepackage',
    name: 'singlepackage',
    component: singlePackage
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
